.svgStyle1 rect {
    fill: rgb(0, 0, 0);
    stroke:rgb(0, 0, 0);
    animation-name: example;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

.svgStyle2 rect {
    fill: none;
    stroke:rgb(0, 0, 0);
    animation-name: example2;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

@keyframes example {
    to {fill: rgb(193, 193, 193);
        stroke:rgb(193, 193, 193);
    }
}
@keyframes example2 {
    to {fill: none;
        stroke:rgb(193, 193, 193);
    }
}